.page-container {
  background-color: none;
  font-family: 'Noto Sans JP', sans-serif;
  color: #777777;
  background-image: url(/PatrickBrown/static/media/background.8f5880a2.webp);
  background-color: none;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: auto;
  clear: both;
  overflow: scroll;
  overflow-x: hidden;

}

::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content-wrap {
  flex: 1 1;
}

a:visited {
  color: none;
}
.page-title {
  width: 100%;
  background: none;
  color: white;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  text-align: center;
  vertical-align: middle;
  padding: 25px;
}

.resume {
  width: 100%;
  background: none;
  color: white;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  text-align: center;
  vertical-align: middle;
  padding: 25px;
}

.google-link:hover {
  background-color: transparent;
  color: #ff0164;
  transition: .15s ease-in-out;
}


@media screen and (max-width: 960px) {
  .page-title {
    /* transform: translate(0, -10%); */
    padding: 25px;
    transition: all 0.1s ease;
  }
}
* {
  font-family: "Montserrat", sans-serif;
}

.card-container {
  width: 80vw;
  margin: auto;
  display: grid;
  justify-content: space-around;
  grid-template-columns: 31% 31% 31%;
  background-color: transparent;
}

.card-box {
  display: flex;
  background-image: linear-gradient(215deg,
      #fc7f4e,
      #e94390,
      #ff0164,
      #be0254,
      #5546cb,
      #3c91c2,
      #14e0f3);
  padding: 8.5px;
  margin-bottom: 50px;
  border-radius: 5px;
}

.card {
  position: relative;
  background-color: #14e0f3;
  padding: 10px;
  width: 95%;
  /* height: 100%; */
  margin: 8px auto;
  border-radius: 5px;
  box-shadow: -5px 7px rgba(6, 64, 70, 0.27);
}

.project-img {
  display: flex;
  vertical-align: middle;
  margin: auto;
  max-width: 90%;
}

.card-body {
  display: block;
  padding-bottom: 3%;
}

.card-title {
  font-size: 30px;
  color: #870dc0;
  text-align: center;
}

.card-text {
  color: #870dc0;
  text-align: center;
  font-size: 16px;
  line-height: 30px;
}

.btn-link {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  color: white;
}

.btn-link:hover {
  color: #ff0164;
}

.card-footer {
  position: relative;
  height: 55px;
  padding-top: 10px;
}

.project-btn {
  position: absolute;
  bottom: 3px;
  list-style: none;
  cursor: pointer;
  background-color: #ff0164;
  font-weight: bold;
  font-size: 16px;
  height: 35px;
  border: none;
  border-radius: 5px;
}

.project-btn:hover {
  border: 2px solid #ff0164;
  background-color: #14e0f3;
  border-radius: 4px;
  transition: all .33s ease-in;
}

.right-btn {
  right: 3px;
  width: 100px;
}

.left-btn {
  left: 3px;
  width: 60px;
}

.project-break {
  background-color: #ff0164;
  color: #ff0164;
  border-style: solid;
  border-width: 2px;
  border-radius: 2px;
  margin: 0.5em auto;
}

@media screen and (max-width: 960px) {
  .card-container {
    width: 100vw;
    margin: auto;
    display: grid;
    grid-template-columns: 47% 47%;
    background-color: transparent;
    /* padding: 10px; */
  }
}

@media screen and (max-width: 640px) {
  .card-container {
    justify-content: center;
    width: 100vw;
    margin: auto;
    display: grid;
    grid-template-columns: 95%;
    background-color: transparent;
    /* padding: 10px; */
  }
}
